.list {
  margin-left: 50px;
}

.tittle {
  margin-left: 50px;
  margin-bottom: 30px;
}

.item:not(:last-child) {
  margin-bottom: 10px;
}

.link {
  display: inline-block;
  text-decoration: none;
  padding: 5px;
  font-weight: 500;
  font-size: 16px;
  border-radius: 5px;
}

.link:hover,
.link:focus {
  background-color: bisque;
}
